<template>
  <el-container id="container">
    <el-header class="el-header">
      Python等高线图绘制
    </el-header>
    <el-container>
      <el-aside style="height: 500px">
        <el-select v-model="dataSourceValue" placeholder="请选择数据源" style="width:100%">
          <el-option
              v-for="item in dataSourceOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="modeValue" placeholder="请选择样式" style="width:100%">
          <el-option
              v-for="item in modeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" :loading=loading style="width: 100%" @click="getContourMap">提交</el-button>
      </el-aside>
      <el-main
          v-loading="loading"
          element-loading-background="rgba(255, 255,255, 0.5)"
          element-loading-text="正在计算,大约需要1min,请稍后...">
        <el-image v-if="!loading" ref="contour_image" fit="full"
                  :lazy="true"
                  :src="picUrl">
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import axios from "axios";

export default {
  name: 'MainPage',
  props: {
    msg: String
  },
  data() {
    return {
      modeOptions: [{
        value: '1',
        label: '3D高程图'
      }, {
        value: '2',
        label: '3D等高线图'
      }, {
        value: '3',
        label: '2D等高线图'
      }, {
        value: '4',
        label: '2D等高线图(无填充)'
      },],
      dataSourceOptions: [{
        value: 'data1',
        label: 'data1'
      },],
      modeValue: '',
      dataSourceValue: '',
      loading: false,
      picUrl: 'http://placekitten.com/350/200',
    }
  },
  methods: {
    getContourMap() {
      this.loading = true
      let source = this.dataSourceValue
      let type = this.modeValue
      if (source.length === 0 || type.length === 0) {
        this.loading = false
        return
      }
      axios.get('http://localhost:8000/contour', {
        params: {
          source: source,
          type: type,
        },
        responseType: "arraybuffer"
      }).then((response => {
        this.loading = false
        const {data, headers} = response
        let blob = new Blob([data], {type: headers['content-type']})
        this.picUrl = URL.createObjectURL(blob)
        // try {
        //   this.picUrl.srcObject = response.data
        //   // this.$refs.contour_image.srcObject = response.data
        // } catch (error) {
        //            // this.$refs.contour_image.src = URL.createObjectURL(new Blob(binaryData, {type: "application/text"}))
        // }
      }))
    },
  }
}
</script>

